 gave an in depth perspective of the energy sector and the immediate challenges they are currently facing.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tOur second objective was to determine consumer perspectives. This objective was accomplished through the use of surveys in order to gauge consumer interests on the energy sector and their interests in producing their own energy. This is essential because utilities are now interested in understanding consumer needs due to the possibility of a liberalized market. With the impending challenge of decentralization, the role of consumers has expanded into that of prosumers and has created an additional interaction between the three main stakeholders. Consumers are also major influencers of regulation in Switzerland, which directly affects the direction of the Swiss energy sector.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tOur third objective was to evaluate how utilities adapt to changes in the industry. This objective was accomplished through interviews with utilities in order to further guide our understanding of companies’ business models and their motivations to adapt. We acquired information regarding ongoing projects, experimentation, and initiatives in order to determine the success and effectiveness of certain business strategies. The strategies and adaptations that utilities are integrating into their business models are allowing us to analyze responses to Switzerland’s changing landscape.\n\t\t"),_c('h1',[_vm._v("Findings")]),_vm._v("\n\t\tOur background research, interviews, and surveys have led to five key findings. First, we "),_c('em',[_vm._v("found the business plan of simply selling electricity is no longer sustainable")]),_vm._v(". In the past, utilities have been able to directly produce, distribute, and sell to consumers. Now with the addition of prosumers, the relationship between utility and consumer has changed. All of our interviews agreed that solely selling electricity is no longer sustainable and Swiss utilities are currently in different stages of transitioning out of that business model.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tAs an expansion of the first finding "),_c('em',[_vm._v("we found utilities need to expand their products and services in order to survive")]),_vm._v(". All interviewed utility companies expressed a need to explore additional markets not only to help raise profits, but also to diversify. As one company said, “It is always better to stand on more than two legs.” The new markets include services such as grid projects, telecommunications, efficiency consulting, e-mobility, and PV solutions.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tAlong with expanding services, "),_c('em',[_vm._v("we found utilities are showing increased interest in understanding consumers and their needs")]),_vm._v(". Currently, Swiss utilities lack a full understanding of their customers. Consumer needs have never been a priority due to the partially monopolized market. Decentralization and overcapacity of the grid are causing a shift in the market in which consumers are affecting the direction of the energy sector. In order to prepare for market shifts, utilities are focusing resources to understand consumer needs, create customer-oriented services, and eventually implement those into their business models.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tSwitzerland’s government is a major influencer in the energy market and "),_c('em',[_vm._v("we found complicated regulations make it difficult for companies to operate")]),_vm._v(". The Swiss energy sector is heavily regulated by the Federal Electricity Commission, restricting utilities from freely experimenting. The political cycle changes approximately every five years and the approval process extends beyond this time frame. The approval process includes substantial paperwork, which further deters utilities from investing in new technologies when regulations would require major design changes even before the project is completed. The fear of drastic policy changes inhibits utilities from new investments and developments.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tAfter distributing consumer surveys, "),_c('em',[_vm._v("we found consumers value low costs and green energy")]),_vm._v(". In our survey, we asked consumers what the most important factor is when they consume electricity. The two most important factors were price and renewability. Although this poses a challenge for utilities to deliver both factors to consumers, utilities now have a better understanding of what customers value.\n\t\t"),_c('h1',[_vm._v("Recommendations")]),_vm._v("\n\t\tBased on our background research, interviews, survey results, and findings, we formulated a list of recommendations for the Smart Grid Plus team at ZHAW and Swiss utilities. These recommendations focus on the importance of the consumer in the changing energy sector and on the need for additional collaboration between academics and utilities. First, "),_c('em',[_vm._v("we recommend utilities research consumer needs and wants")]),_vm._v(". The role of consumers is becoming more influential in the energy sector, but there is a lack of research on consumers. Therefore, utilities should allocate resources to conduct their own research on consumer needs and wants in order to design customer-oriented services.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tAfter understanding consumer needs, "),_c('em',[_vm._v("we recommend that utilities develop strategies for implementing new technologies and integrate customer-oriented services into their existing business models")]),_vm._v(". In order for utilities to smoothly transition with the changing Swiss energy sector, it is essential for them to innovate and adapt their current business models instead of creating new ones. The results from our interviews showed that developing departments dedicated to exploring niche technologies and allocating resources to create strategies is crucial.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tAdditionally, "),_c('em',[_vm._v("we recommend that utilities increase customer outreach in the forms of marketing and education")]),_vm._v(". As utilities plan to add customer-oriented service to their business models, customer outreach is essential for those services to be successful. This will establish a line of communication with consumers that provide many benefits to utilities. Customers may be more responsive to utilities’ services and support new projects and initiatives.\n\t\t"),_c('br'),_c('br'),_vm._v("\n\t\tA rising challenge in the energy sector involves congestion in the grid during peak production hours and "),_c('em',[_vm._v("we recommend the development of peak shaving regulations")]),_vm._v(". Peak shaving is a strategy for limiting injection into the grid during hours of peak production by restricting the maximum potential of prosumer installations. With current regulations, utilities are required to accept 100% of prosumer power production and the generated power is automatically accepted into the grid. Utilities must expand grid capacity in order to support the extra load from prosumer contributions. Peak shaving of prosumer power production could potentially mitigate the burden of grid expansions.\n \t\t"),_c('br'),_c('br'),_vm._v("\n\t\tFinally, "),_c('em',[_vm._v("we recommend that ZHAW conducts additional research into negative and positive effects of departmental company structures in the Swiss energy sector")]),_vm._v(". Many utility companies are comprised of multiple independent departments that do not collaborate effectively. This leads to gaps in communications and can result in the repetition of work and the loss of valuable time and money. Although our interviews revealed both positive and negative effects of the departmental company structure, it is unclear whether compartmentalization is beneficial or detrimental overall.\n\t\t"),_c('h1',[_vm._v("Conclusion")]),_vm._v("\n\t\tBy analyzing the current changes in the Swiss energy sector, we have provided utilities with recommendations to help contribute to their survivability in the market. A rubric for grading the survivability of Swiss electric utilities is included in this paper as a self-assessment and scores each utility based on the four pillars of a business model. It provides a list of criteria that each utility should strive for in order to limit potential risk in the market.\n  ")])}]
